import React, { useState, useEffect } from 'react'
import axios from 'axios'
import noteService from './components/promise'

 const Filter = ({filter,handleChange}) => {
	  return (
	   <div>
          Filter: <input value={filter} onChange={handleChange} />
        </div>
	  )
  }
  const PersonForm = ({name,handleName,number,handleNumber,addNewName}) =>{
	  return (
	   <form onSubmit={addNewName}>
        <div>
          name: <input value={name} onChange={handleName} />
        </div>
		 <div>number: <input value={number} onChange={handleNumber} /></div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
	  )
	  
  }
  
   const Persons = ({persons,setPersons}) =>{
	  return(
	  <div>
	  {persons.map( person => 
	  <p key={person.name}>{person.name} {person.number}
	  <button onClick={() =>{noteService.del(person.id).then(response => {
		  noteService.getAll().then(response => setPersons(response))
	  })
	 
	  }}> delete</button>
	  </p>)
	  }
	  </div>
	  )
  }
  
  const Notification = ({message,cssStyle}) => {
	  if(message === null){
		  return null
	  }
	  
	  
	  return(
	  <div style={cssStyle}>
		  {message}
	  </div>
	  )
  }

const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ newFilter, setNewFilter ] = useState('')
  const [message,setMessage] = useState('')
  const [errorMessage,setErrorMessage]= useState('')
  
  const greenStyle = {
		  color: 'green',
		  fontSize: 40
	  }
	  
   const redStyle = {
		  color: 'red',
		  fontSize: 40
	  }
  
  useEffect( () => {
	  console.log("effect")
	  noteService.
	  getAll().then(response => {
		  setPersons(response)
		  console.log(response)
	  })
	  
	 
	  
  },[])
  
  console.log('render',persons.length,'persons')
  
  const personsToShow = persons.filter(person => person.name.includes(newFilter))
  
  
 const addNewName = (event) => {
	 event.preventDefault()
	 const person = { name: '',number:'' }
	 person.name = newName 
	 person.number= newNumber
	 console.log(persons.map(person => person.name))
	 if(persons.map(person => person.name).includes(person.name)) {
		 if(window.confirm(`${person.name} already exits in the system, do you want to replace the number`)){
			 const newPerson = persons.find(per => per.name === person.name)
			 const changedPerson = {...newPerson,number:newNumber}
			 noteService.update(changedPerson.id,changedPerson).then(response =>{
		     noteService
	         .getAll().then(response => {setPersons(response);
	         setMessage(`${person.name} number has changed`) ;
	         setTimeout(() => {
             setMessage(null)
             }, 5000);})
	           }
			  ).catch(error => {
				  setErrorMessage(`${person.name} has been deleted from the system!`);
				  noteService.getAll().then(response => setPersons(response));
				  setTimeout(() => {
				  setErrorMessage(null)
				  }, 5000)
				  
			  })
			  
			 return
			 
		 }
			 
		 return 
	 }
	 
	 console.log(newName,newNumber)
	 noteService
	 .create(person).then(response =>{
		 	 noteService
	 .getAll().then(response => {setPersons(response);
	 setMessage(`${person.name} has been added`) ;
	         setTimeout(() => {
             setMessage(null)
	 }, 5000);})
	 })
	 setNewName('')
	 setNewNumber('')

	 
 }

 

 const handleNameChange = (event) => {
	  console.log(event.target.value)
	  setNewName(event.target.value)
	  
  }
  
  const handleNumberChange = (event) => {
	  console.log(event.target.value)
	  setNewNumber(event.target.value)
	  
  }
  const handleFilterChange = (event) => {
	  console.log(event.target.value)
	  setNewFilter(event.target.value)
  }




  return (
    <div>
	 <Filter filter={newFilter} handleChange={handleFilterChange}/>
     <Notification message={message} cssStyle={greenStyle}/>
	 <Notification message={errorMessage} cssStyle={redStyle}/>
      <h2>Phonebook</h2>
	 
      <PersonForm name={newName} handleName={handleNameChange} 
	   number={newNumber} handleNumber={handleNumberChange} addNewName={addNewName}/>
      <h2>Numbers</h2>
      <Persons persons={personsToShow} setPersons={setPersons}/>
    </div>
  )
}

export default App